<template>

  <div class="">
    <v-app-bar dark clipped-left color="primary" flat>
      <v-app-bar-nav-icon @click="drawer=!drawer" />
      <v-toolbar-title>
        <h3>Loso</h3>
      </v-toolbar-title>
      <v-spacer/>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
                    <v-btn v-on="on" icon>
                      <v-icon>fa-user</v-icon>
                    </v-btn>
                  </template>
        <v-list>
          <v-list-item :to="'/account/'+navtab.to" user v-for="(navtab,index) in navtabs.slice(1,3)" :key="index">
            <v-list-item-avatar>
              <v-icon color="primary" size="16">
                fa-{{navtab.icon}}
              </v-icon>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>
                {{navtab.title}}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item @click="logout">
            <v-list-item-avatar>
              <v-icon size="16" color="primary">fa-lock</v-icon>
            </v-list-item-avatar>
            <v-list-item-title>
              <v-list-item-title>
                Log Out
              </v-list-item-title>
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
    <v-navigation-drawer clipped v-model="drawer" app>
      <v-toolbar flat color="transparent">
        <v-spacer />
        <v-btn @click="drawer = !drawer" icon>
          <v-icon color="primary">fa-times</v-icon>
        </v-btn>
      </v-toolbar>
      <v-card elevation-0 class="mt-3" flat>
        <v-card-text class="text-center">
          <v-avatar size="96" color="primary">
            <v-icon dark size="48">fa-user</v-icon>
          </v-avatar>
          <h1 class="my-3 text-capitalize">{{$store.getters.user.username}}</h1>
        </v-card-text>

      </v-card>
      <v-list>
        <v-list-item :to="'/account/'+navtab.to"
                     v-for="(navtab,index) in navtabs"
                     :key="index"
                     >
          <v-list-item-avatar>
            <v-icon size="16" color="primary">fa-{{navtab.icon}}</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>{{navtab.title}} </v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-icon size="16" color="primary">fa-angle-right</v-icon>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-content>
      <router-view />
    </v-content>
  </div>

</template>



<script>

  export default {
    data() {
      return {
        drawer: true,
        form_add: false,
        navtabs: [
          { title: 'Home', icon: 'home', to: '' },
          { title: 'Profile', icon: 'info-circle', to: 'profile' },
          { title: 'Wallet', icon: 'credit-card', to: 'wallet' },
          { title: 'Matrix', icon: 'adn', to: 'matrix' },
          { title: 'About', icon: 'home', to: 'about' }
        ],
        feedForm: {
          text: '',
          loading: false
        }
      }
    },
    methods: {
      navigateHome() {
        this.$router.replace({ name: 'login' })
      },
      logout() {
        this.$store.dispatch('logOut')
        this.navigateHome()
      }
    },
    beforeRouteEnter(to, from, next) {
      next(self => {
        if(!self.$store.getters.downlineIsLoaded && self.$store.state.user.type>0){
          self.$store.dispatch('downlineUpdate')
        }
      })
    },
    beforeRouteUpdate(to,from,next){
      this.$store.commit('updateAccess')     
      if(this.$store.getters.auth){
        if(this.$store.getters.level>0){
          this.$store.dispatch('downlineUpdate')
          next()
        }
        else if(to.name=='payment' || to.name=='lock'){
          next()
        }else{
          next({path:'lock'})
        }
      }else{
        next({path:'login'})
      }
    }
  }

</script>
